const ErrorPage = {
    Default: ErrorDefault,
    Custom: ErrorCustom,
}

let loadings = [];
eventbus.on('loading', () => {
    loadings.push(new Loading())
});


eventbus.on('loading.lazy', () => {
    loadings.push(new Loading({data:{mode:'lazy'}}))
});

eventbus.on('loaded', () => {
    if(loadings[0]){
        loadings[0].$destroy()
        loadings.shift();
    }
});

//以插件形式存在的Modal
Modal.install = function(Vue, options){
    Vue.prototype.$Modal = function(options, ok, cancel){
        this.$nextTick( () => {
            //恢复默认
            var gomeModal = this.$root.$refs['gome-ui-kit-modal'];
            gomeModal.htmlContent = ''
            gomeModal.conent = ''
            gomeModal.title = ''
            gomeModal.ok = ''
            gomeModal.cancel = ''
            gomeModal.hasClose = false
            
            //显示
            gomeModal.show = true;
            //重新赋值
            for(var key in options){
                gomeModal[key] = options[key];
            }
            //侦听成功的消息
            // if(ok == 'ajaxc'){
            //         console.log('1111')
            // }else{
            gomeModal.$on('ok',() => {
                if(typeof(ok) === 'function'){
                    ok()
                }else{
                    gomeModal.show = false;                    
                }
            })                    
            gomeModal.$on('cancel',() => {
                gomeModal.show = false;
            })
        })
    }
}
Vue.use(Modal)

