<template>
    <div class="page" :style="style">
        <slot></slot>
    </div>
</template>
<script>
    import Vue from 'vue';
    import scroller from '../scroller/scroller.vue';
    export default Vue.extend({
        components: {

        },
        data () {
            return {
                style: {
                    'min-height': document.documentElement.clientHeight + 'px'
                },
            }
        }
    })
</script>
<style lang="less">
    .page {
        width: 100%;
    }
</style>


